* {
  margin: 0;
  padding: 0; }

body {
  font-family: "微软雅黑", Verdana, Geneva, sans-serif, Arial;
  width: 100%;
  text-align: left;
  margin: 0 auto;
  background: #f3f3f3;
  font-size: 14px;
  color: #474849; }

ul, li, ol, dl {
  list-style-type: none;
  color: #474849; }

a {
  text-decoration: none;
  color: #474849; }
  a:hover, a:active, a:focus {
    text-decoration: none; }

h2, h3, h4, h5, b {
  font-weight: normal; }

select, input {
  height: 28px;
  border: 1px solid #999;
  border-radius: 4px;
  -moz-border-radius: 4px;
  /* 老的 Firefox */ }

img {
  border: none;
  padding: 0;
  margin: 0; }

.pl-list li {
  padding-bottom: 4px; }
  .pl-list li img {
    width: 100%;
    height: 100%; }

@font-face {
  font-family: 'icomoon';
  src: url("../fonts/icomoon.eot?doj3xu");
  src: url("../fonts/icomoon.eot?doj3xu#iefix") format("embedded-opentype"), url("../fonts/icomoon.ttf?doj3xu") format("truetype"), url("../fonts/icomoon.woff?doj3xu") format("woff"), url("../fonts/icomoon.svg?doj3xu#icomoon") format("svg");
  font-weight: normal;
  font-style: normal; }

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.icon-cart:before {
  content: "\e900"; }

.icon-home:before {
  content: "\e901"; }

.icon-my:before {
  content: "\e902"; }

.icon-search:before {
  content: "\e903"; }

.icon-shop:before {
  content: "\e904"; }

.main-nav {
  position: fixed;
  width: 100%;
  bottom: 0;
  background: #fff;
  border-top: 1px solid #CCC;
  margin: 0; }
  .main-nav li {
    width: 20%;
    padding: 3px 0;
    float: left;
    text-align: center; }
    .main-nav li a i {
      font-size: 20px; }
    .main-nav li a span {
      line-height: 16px;
      display: block; }
    .main-nav li.active a, .main-nav li.active span {
      color: #fd9427; }

.user-info li {
  background-color: #fff;
  border-top: 1px solid #ddd;
  overflow: hidden;
  padding: 0 15px; }
  .user-info li:last-child {
    border-bottom: 1px solid #ddd; }
  .user-info li span {
    display: inline-block;
    line-height: 50px;
    font-size: 16px; }
    .user-info li span strong {
      margin: 0;
      display: block;
      background-color: #00a0e9;
      padding: 5px;
      border-radius: 26px;
      float: left;
      line-height: 16px;
      margin-top: 12px; }
